<template>
    <mine-card :title="$t('dictionary.registerDomain')" :iconImg="require('@/assets/images/icons/register.png')">
        <div class="mine-register-wrap">
            <div class="mine-register-input">
                <input v-model="form.value" type="text" :placeholder="$t('placeholder.domain')">
                <span>.ont</span>
            </div>
            <!-- <a-input style="margin-right: 15px;" size="large" :placeholder="$t('placeholder.domain')"></a-input> -->
            <a-button size="large" :loading="loading" type="primary" @click="submit">{{$t('dictionary.register')}}</a-button>
        </div>

        <span slot="action" style="font-size: 14px;" @click="info">{{$t('rule.btn')}}</span>
    </mine-card>
</template>

<script>
import domainService from '@/services/domain'
import MineCard from './MineCard'
import { mapActions, mapGetters } from 'vuex'

export default {
    components: {
        MineCard
    },
    data() {
        return {
            loading: false,
            form: {
                value: ''
            }
        }
    },
    methods: {
        ...mapActions(['getDomains']),
        submit() {
            const domain = this.form.value
            if(!domain) return this.$message.warning(this.$t('dictionary.domainNull'))
            if(!this.$util.verifyDomain(domain)) return this.$message.warning(this.$t('dictionary.domainIllegalFormat'))
            this.loading = true
            domainService.rgister(domain)
                .then(res => {
                    const code = parseInt(res.result[0][1], 16)
                    if(code === 14) {
                        this.$message.success(this.$t('dictionary.registerSuccess'))
                        this.getDomains()
                    } 
                    else this.$message.warning(this.$t('register.' + this.$config.registerStatus[code]))

                })
                .catch(err => {
                    console.log(err)
                    this.$message.warning(this.$t('dictionary.registerSuccess'))
                })
                .finally(() => this.loading = false)
        },
         info() {
            const h = this.$createElement
            this.$info({
                title: this.$t('rule.title'),
                content: h('div',{}, [
                    h('p', this.$t('rule.0')),
                    h('p', this.$t('rule.1')),
                    h('p', this.$t('rule.2'))
                ]),
                onOk() {},
            })
        },
    }
}
</script>

<style lang="less">
.mine-register-wrap {
    display: flex; 
    padding: 20px;
}
.mine-register-input {
    width: 0;
    flex: 1;
    display: flex;
    align-items: center;
    margin-right: 15px;
    padding-right: 15px;
    // background-color: #f5f6f7;
    border-radius: 4px;
    border: 1px solid #eee;
    input {
        width: 100%;
        flex: 1;
        padding: 8px 15px;
        background-color: transparent;
        border: none;
        resize: none;
        outline: 0;
        font-size: 16px;
        line-height: 25px;
    }
}
</style>
