<template>
    <mine-card :title="$t('dictionary.walletAddress')" :iconImg="require('@/assets/images/icons/wallet.png')">
        <div class="wallet-address-wrap">
            <div class="wallet-address"
                v-clipboard:copy="address"
                v-clipboard:success="onCopySuccess"
                v-clipboard:error="onCopyError">
                {{address}}
                <img :src="require('@/assets/images/icons/copy.png')">
            </div>
            <div class="wallet-address-info">
                <div>
                    <div>{{$t('dictionary.donationed')}}{{donatTotal}} ONT</div>
                    <a-button type="primary" ghost @click="$refs.donation.open()">{{$t('dictionary.donation')}}</a-button>
                </div>
                <div>
                    <div>{{$t('dictionary.invitered')}}{{inviterTotal}} </div>
                    <a-button type="primary" ghost @click="$refs.inviter.open()">{{$t('dictionary.inviter')}}</a-button>
                </div>
            </div>
        </div>
        <donation ref="donation"></donation>
        <inviter ref="inviter"></inviter>
    </mine-card>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import MineCard from './MineCard'
import Donation from '@/components/modal/Donation'
import Inviter from '@/components/modal/Inviter'


export default {
    components: {
        MineCard,
        Donation,
        Inviter
    },
    methods: {
        onCopySuccess(e) {
            this.$message.success(this.$t('dictionary.copySuccess'))
        },
        onCopyError() {
            this.$message.success(this.$t('dictionary.copyError'))
        }
    },
    computed: {
        ...mapGetters(['address', 'donatTotal', 'inviterTotal'])
    }
}
</script>

<style lang="less" scoped>
.wallet-address-wrap {
    padding: 20px;
    text-align: center;
}
.wallet-address {
    display: inline-block;
    padding: 10px;
    background-color: #f5f6f7; 
    border-radius: 4px;
    cursor: pointer;
    img {
        height: 1em;
    }
    * {
        vertical-align: middle
    }
}
.wallet-address-info {
    display: flex;
    padding-top: 20px; 
    &>div {
        width: 0;
        flex: 1;
        &>div {
            margin-bottom: 10px;
        }
    }
}
</style>
