<template>
    <mine-card :title="$t('dictionary.transfer1')" :iconImg="require('@/assets/images/icons/transfer.png')">
        <div class="transfer1-wrap">
            <div class="transfer1-item">
                <span>{{$t('dictionary.assets')}}</span>
                <a-select v-model="type" size="large">
                    <a-select-option v-for="item in $config.currency" :key="item.name" :value="item.name">{{item.name}}</a-select-option>
                </a-select>
            </div>
             <div class="transfer1-item">
                <span>{{$t('dictionary.address')}}</span>
                <a-input v-model="form.address" size="large" :maxlength="34" :placeholder="$t('placeholder.address')"></a-input>
            </div>
             <div class="transfer1-item">
                <span>{{$t('dictionary.amount')}}</span>
                <a-input-number v-model="form.value" size="large" :precision="$config.currency[type].precision" :max="balance[type]" :min="0" placeholder="" style="width: 150px"></a-input-number>
            </div>
            <div style="text-align: right">
                <a-button type="primary" size="large" icon="check" @click="submit">{{$t('dictionary.transfer1')}}</a-button>
            </div>
        </div>
    </mine-card>
</template>

<script>
import walletServices from '@/services/wallet' 
import domainServices from '@/services/domain' 
import { mapActions, mapGetters } from 'vuex'
import MineCard from './MineCard'

export default {
    components: {
        MineCard
    },
    data() {
        return {
            loading: false,
            type: this.$config.wallet.types[0],
            form: {
                value: 1,
                address: ''
            }
        }
    },
    methods: {
        ...mapActions(['getBalance']),
        submit() {
            const address = this.form.address
            if(!this.form.value) return this.$message.warning(this.$t('dictionary.amountNull'))
            if(!address) return this.$message.warning(this.$t('dictionary.addressNull'))
            if(address.length === 34) {
                const hex = this.$util.address2Hex(address)
                if(!hex) return this.$message.warning(this.$t('dictionary.addressIllegal'))
                // if(hex === this.scriptHash)  return this.$message.warning(this.$t('dictionary.transfer1NotMe'))
                // 
                this.transfer(address)
            } else {
                if(!this.$util.verifyDomainAll(address)) return this.$message.warning(this.$t('dictionary.domainIllegal'))
                // 
                domainServices.resolve(address.split('.')[0], this.$config.domainResolveActionCode.PROTOCOL_ADDRESS).then(res => {
                    console.log(res)
                    if(!res) this.$message.warning(this.$t('dictionary.domainNotResolve'))
                    else this.transfer(this.$util.utf8HexToStr(res))

                }).catch(err => console.log(err))
            }
        },
        transfer(address = '') {
            this.$confirm({
                title: this.$t('dictionary.transfer1Cfm'),
                content: `${address}`,
                onOk: () => {
                    this.loading = true
                    const curent = this.$config.currency[this.type]
                    const amount = this.form.value * Math.pow(10, curent.precision)
                    const hex = this.$util.address2Hex(address)
                    walletServices.transfer(amount, hex, curent.contract, this.type).then(res => {
                        this.$message.success(this.$t('dictionary.transfer1Success'))
                        this.getBalance()
                    }).catch(err => {
                        console.log(err)
                        this.$message.warning(this.$t('dictionary.transfer1Error'))
                    }).finally(() => this.loading = false)
                },
                onCancel() {
                    console.log('Cancel')
                },
            })
        },
        cancel() {
            this.close()
        },
    },
    computed: {
        ...mapGetters(['scriptHash', 'balance'])
    }
}
</script>

<style lang="less">
.transfer1-wrap {
    padding: 20px;
}
.transfer1-item {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
    &:last-child {
        margin: 0;
    }
    &>span:first-child {
        width: 60px;
        margin-right: 15px;
    }
}
</style>
