<template>
    <a-modal
        :title="$t('home.nav.inviter')"
        :width="600"
        :visible="show"
        :footer="null"
        @cancel="close"
        >
    <div class="inviter-wrap">
        <a-input v-model="link" :placeholder="$t('placeholder.link')"></a-input>
        <span>
            <a-button type="primary"
                v-clipboard:copy="link"
                v-clipboard:success="onCopySuccess"
                v-clipboard:error="onCopyError">{{$t('dictionary.copy')}}</a-button>
        </span>
    </div>
    </a-modal>
</template>

<script>
import walletServices from '@/services/wallet'
import { mapGetters } from 'vuex'

export default {
    data() {
        return {
            show: false,
            link: ''
        }
    },
    methods: {
        open() {
            this.link = `http://${window.location.host}?ref=${this.address}`
            this.show = true
        },
        close() {
            this.show = false
        },
        onCopySuccess(e) {
            this.$message.success(this.$t('dictionary.copySuccess'))
        },
        onCopyError() {
            this.$message.success(this.$t('dictionary.copyError'))
        }
    },
    computed: {
        ...mapGetters(['address'])
    }
}
</script>

<style lang="less">
.inviter-wrap {
    display: flex;
    align-items: center;
    & > span {
        margin-left: 10px;
    }
}
</style>
