<template>
     <a-modal
        :title="$t('dictionary.setresolve') + '(' + domain + '.ont)'"
        :visible="show"
        @ok="ok"
        :confirmLoading="loading"
        @cancel="cancel"
        >
      <div class="donation-wrap">
            <div class="donation-title" v-if="codeKey">{{$t('resolve.' + codeKey)}}</div>
            <div>
                 <a-textarea v-model="form.value" :placeholder="$t('placeholder.resolve')" :autosize="{ minRows: 4, maxRows: 8 }"></a-textarea>
            </div>
      </div>
    </a-modal>
</template>

<script>
import domainServices from '@/services/domain' 
import { mapActions, mapGetters } from 'vuex'

export default {
    data() {
        return {
            show: false,
            loading: false,
            domain: '',
            code: 1,
            codeKey: '',
            index: 0,
            form: {
                value: ''
            }
        }
    },
    computed: {
        ...mapGetters(['scriptHash'])
    },
    methods: {
        ...mapActions(['getDomains']),
        open({item, index}) {
            this.index = index
            this.code = item.code
            this.domain = item.name
            this.codeKey = item.codeKey
            this.form.value = item.data
            this.show = true
        },
        close() {
            this.show = false
            this.form.value = ''
        },
        ok() {
            if(!this.form.value) return this.$message.warning(this.$t('dictionary.resolveNull'))
            if(!this.$util.address2Hex(this.form.value)) return this.$message.warning(this.$t('dictionary.addressIllegal'))
            this.loading = true
            domainServices.setResolve(this.domain, this.code, this.form.value).then(res => {
                const code = parseInt(res.result[0][1], 16)
                if(code === 14) {
                    this.$message.success(this.$t('dictionary.resolveSuccess'))
                    this.close()
                    this.$emit('on-success', {index: this.index, code: this.code})
                    this.getDomains()
                }
                else this.$message.warning(this.$t('register.' + this.$config.registerStatus[code]))
            })
            .catch(err => {
                console.log(err)
                this.$message.warning(this.$t('dictionary.resolveError'))
            })
            .finally(() => this.loading = false)
        },
        cancel() {
            this.close()
        },
        onChange(value) {
            // this.form.value = parseInt(value)
        }
    }
}
</script>

<style lang="less">
.donation-wrap {
    font-size: 20px;
}
.donation-title {
    margin-bottom: 10px;
    font-size: 16px;
}
.donation-input {
    width: 8em;
    margin-right: 5px;
}
.donation-balance {
    font-weight: bold;
}
</style>
