<template>
    <div>
        <mine-card :title="$t('dictionary.assets')" :iconImg="require('@/assets/images/icons/wallet.png')">
            <div class="balance-wrap">
                <div class="balance-item" v-for="(item, key) in balance" :key="key">
                    <span>{{key}}</span>
                    <span>{{item || 0}}</span>
                </div>
            </div>
            <span slot="action" class="mine-address" 
                v-clipboard:copy="address"
                v-clipboard:success="onCopySuccess"
                v-clipboard:error="onCopyError">
                {{$t('dictionary.address')}}{{address}} 
                <img :src="require('@/assets/images/icons/copy.png')">
                </span>
        </mine-card>
        <transfer></transfer>
    </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import MineCard from './MineCard'
import Transfer from './Transfer'

let timer = null

export default {
    components: {
        MineCard,
        Transfer
    },
    mounted() {
        this.getData()
        this.update()
    },
    methods: {
         ...mapActions(['getBalance']),
        getData() {
            this.getBalance()
        },
        update() {
            this.closeUpdate()
            timer = setTimeout(() => {
                this.getData()
                this.update()
            }, 5000)
        },
        closeUpdate() {
            clearTimeout(timer)
            timer = null
        },
        onCopySuccess(e) {
            this.$message.success(this.$t('dictionary.copySuccess'))
        },
        onCopyError() {
            this.$message.success(this.$t('dictionary.copyError'))
        }
    },
    computed: {
        ...mapGetters(['balance', 'address'])
    },
    destroyed() {
        this.closeUpdate()
    }
}
</script>

<style lang="less">
.balance-wrap {
    padding: 5px 0
}
.balance-item {
    height: 50px;
    display: flex;
    align-items: center;
    padding: 0 20px;
    font-size: 18px;
    &>span:first-child {
        display: inline-block;
        width: 60px;
        margin-right: 15px;
    }
    &>span:last-child {
        font-size: 24px;
    }
}
.mine-address {
    font-size: 14px;
    img {
        height: 1em;
    }
    * {
        vertical-align: middle
    }
}
</style>
