<template>
    <div class="mine-page">
        <div class="mine-page-header">
            <img class="mine-page-logo" :src="$config.logo.black"  @click="toMain">
            <!-- <span @click="toMain">LOGO</span> -->
            <span @click="toMain">{{$t('dictionary.backHome')}}</span>
        </div>
        <div class="mine-page-body">
            <div class=""></div>
            <div class="mine-page-tab">
                <div class="mpt-nav">
                    <span v-for="item in navs" :key="item.key" :class="{active: current === item.key}" @click="switchTab(item.key)">{{$t('dictionary.' + item.key)}}</span>
                </div>
                <div class="mpt-content">
                    <component :is="current"></component>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Domain from './Domain'
import Wallet from './Wallet'

export default {
    components: {
        Domain,
        Wallet
    },
    data() {
        return {
            current: 'wallet',
            navs: [
                {
                    key: 'wallet'
                },
                {
                    key: 'domain'
                }
            ]
        }
    },
    mounted() {
        this.current = this.$route.query.type || 'wallet'
    },
    methods: {
        switchTab(key) {
            this.current = key
        },
        toMain() {
            this.$router.push({name: 'home'})
        },
    }
}
</script>

<style lang="less">
.mine-page {
    width: 100%;
    min-height: 100%;
    background-color: #f3f3f3;
}
.mine-page-header {
    height: 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 50px;
    border-bottom: 1px solid #ddd;
    &>span,&>img {
        cursor: pointer;
    }
}
.mine-page-logo {
    height: 40px;
}
.mine-page-body {
    width: 1000px;
    margin: auto;
    padding-top: 30px;
    // background-color: #fff;
}
.mpt-nav {
    text-align: center;
    font-size: 16px;
    & > span {
        width: 10em;
        display: inline-block;
        padding: 10px;
        background-color: #fff;
        border-radius: 50px;
        border: 2px solid #fff;
        cursor: pointer;
        &:first-child {
            margin-right: 15px;
        }
        &.active {
            border-color: #1890ff;
            color: #1890ff;
        }
    } 
}
.mpt-content {
    padding-top: 30px;
}
</style>
