<template>
    <mine-card :title="$t('dictionary.domain')" :iconImg="require('@/assets/images/icons/domain_black.png')">
        <div class="domain-item" v-for="(item, index) in list" :key="index">
            <div class="domain-item-header">
                <span>{{item.name}}.ont</span>
                <span><template v-if="item.time">{{$t('dictionary.registerTime')}}{{item.time}}</template></span>
            </div>
            <div class="domain-item-body">
                <div class="domain-item-address">{{$t('dictionary.address')}}{{item.data || $t('dictionary.notSet')}}</div>
                <div class="domain-item-action">
                    <a-button class="domain-item-action-btn" icon="setting" @click="$refs.setResolveDomain.open({item, index})">{{$t('dictionary.setresolve')}}</a-button>
                    <a-button class="domain-item-action-btn" icon="file-sync" @click="$refs.transferDomain.open(item)">{{$t('dictionary.transfer2')}}</a-button>
                    <a-button class="domain-item-action-btn" icon="delete" @click="deleteDomain(item)">{{$t('dictionary.destroy')}}</a-button>
                </div>
            </div>
        </div>
        <transfer-domain ref="transferDomain"></transfer-domain>
        <set-resolve-domain ref="setResolveDomain"></set-resolve-domain>
    </mine-card>
</template>

<script>
// import moment from 'moment'
import { mapGetters, mapActions } from 'vuex'
import domainService from '@/services/domain'
import MineCard from './MineCard'
import TransferDomain from '@/components/modal/TransferDomain'
import SetResolveDomain from '@/components/modal/SetResolveDomain'

function formatTime(date) {
    const _date = new Date(date)
    if(!_date.getFullYear()) return ''
    return `${_date.getFullYear()}-${_date.getMonth() + 1}-${_date.getDate()} ${_date.getHours()}:${_date.getMinutes()}:${_date.getSeconds()}`
}

export default {
    components: {
        MineCard,
        TransferDomain,
        SetResolveDomain
    },
    data() {
        return {
            list: []
        }
    },
    mounted() {
        this.genDomainList()
    },
    methods: {
        ...mapActions(['getDomains']),
        async genDomainList() {
            const codes = this.$config.domainResolveActionCode
            const keys = Object.keys(codes)
            const code = this.$config.domainResolveActionCode[keys[0]]
            const arr = await this.domains.map(async item => {
                return {
                    name: item.name,
                    time: formatTime(item.time * 1000),
                    data: this.$util.utf8HexToStr(await domainService.resolve(item.name, code)),
                    code,
                    codeKey: keys[0]
                }
            })
            this.list = await Promise.all(arr.map(async value => value))
        },
        deleteDomain(item) {
            this.$confirm({
                title: '',
                content: `${item.name}.ont`,
                okType: 'danger',
                onOk: () => {
                    domainService.drop(item.name)
                    .then(res => {
                        const code = parseInt(res.result[0][1], 16)
                        if(code === 14) {
                            this.$message.success(this.$t('dictionary.deleteSuccess'))
                            this.getDomains()
                        }
                        else this.$message.warning(this.$t('register.' + this.$config.registerStatus[code]))
                    })
                    .catch(err => {
                        console.log(err)
                        this.$message.warning(this.$t('dictionary.actionError'))
                    })
                },
                onCancel() {
                    console.log('Cancel')
                },
            });
        },
    },
    computed: {
        ...mapGetters(['domains'])
    },
    watch: {
        domains() {
            this.genDomainList()
        }
    }
}
</script>

<style lang="less">
.domain-item {
    padding: 20px;
    border-bottom: 1px solid #eee;
}
.domain-item-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    &>span:first-child {
        font-size: 18px;
        font-weight: 600;
    }
}
.domain-item-body {
    // text-align: center;
}
.domain-item-address {
    padding: 10px 0;
}
.domain-item-action {
    display: flex;
    // &>div {
    //     width: 0;
    //     flex:1;
    // }
}
.domain-item-action-btn {
    margin-right: 15px;
}
</style>
