<template>
     <a-modal
        :title="$t('dictionary.transfer3') + '(' + domain + '.ont)'"
        :visible="show"
        @ok="ok"
        :confirmLoading="loading"
        @cancel="cancel"
        >
      <div class="donation-wrap">
            <div class="donation-title">{{$t('dictionary.address')}}</div>
            <div>
                 <a-input v-model="form.value" size="large" :maxlength="34" :placeholder="$t('placeholder.address')"></a-input>
            </div>
      </div>
    </a-modal>
</template>

<script>
import domainServices from '@/services/domain' 
import { mapActions, mapGetters } from 'vuex'

export default {
    data() {
        return {
            show: false,
            loading: false,
            domain: '',
            form: {
                value: ''
            }
        }
    },
    computed: {
        ...mapGetters(['scriptHash'])
    },
    methods: {
        ...mapActions(['getDomains']),
        open(item) {
            this.domain = item.name
            this.show = true
        },
        close() {
            this.show = false
            this.form.value = ''
        },
        ok() {
            const address = this.form.value
            if(!address) return this.$message.warning(this.$t('dictionary.addressNull'))
            if(address.length === 34) {
                const hex = this.$util.address2Hex(address)
                if(!hex) return this.$message.warning(this.$t('dictionary.addressIllegal'))
                // if(hex === this.scriptHash)  return this.$message.warning(this.$t('dictionary.transfer1NotMe'))
                // 
                this.transfer(address)
            } else {
                if(!this.$util.verifyDomainAll(address)) return this.$message.warning(this.$t('dictionary.domainIllegal'))
                // 
                domainServices.resolve(address.split('.')[0], this.$config.domainResolveActionCode.PROTOCOL_ADDRESS).then(res => {
                    console.log(res)
                    if(!res) this.$message.warning(this.$t('dictionary.domainNotResolve'))
                    else this.transfer(this.$util.utf8HexToStr(res))

                }).catch(err => console.log(err))
            }
        },

        transfer(address = '') {
            this.$confirm({
               title: this.$t('dictionary.transfer3Cfm'),
                content: `${address}`,
                onOk: () => {
                    this.loading = true
                    const hex = this.$util.address2Hex(address)
                    domainServices.transfer(hex, this.domain).then(res => {
                        const code = parseInt(res.result[0][1], 16)
                        if(code === 14) {
                            this.$message.success(this.$t('dictionary.transfer3Success'))
                            this.close()
                            this.getDomains()
                        } 
                        else this.$message.warning(this.$t('register.' + this.$config.registerStatus[code]))
                    }).catch(err => {
                        console.log(err)
                        this.$message.warning(this.$t('dictionary.transfer3Error'))
                    }).finally(() => this.loading = false)
                },
                onCancel() {
                    console.log('Cancel')
                },
            })
        },

        cancel() {
            this.close()
        },
        onChange(value) {
            // this.form.value = parseInt(value)
        }
    }
}
</script>

<style lang="less">
.donation-wrap {
    font-size: 20px;
}
.donation-title {
    margin-bottom: 10px;
    font-size: 16px;
}
.donation-input {
    width: 8em;
    margin-right: 5px;
}
.donation-balance {
    font-weight: bold;
}
</style>
