<template>
    <div>
        <wallet-address></wallet-address>
        <register></register>
        <domain-list></domain-list>
    </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import TransferDomain from '@/components/modal/TransferDomain'
import MineCard from './MineCard'
import WalletAddress from './WalletAddress'
import Register from './Register'
import DomainList from './DomainList'

let timer = null

export default {
    components: {
        MineCard,
        WalletAddress,
        Register,
        DomainList
    },
    mounted() {
        this.getData()
        this.update()
    },
    methods: {
        ...mapActions(['getDomains', 'getDonatTotal', 'getInviterTotal']),
        getData() {
            this.getDomains()
            this.getDonatTotal()
            this.getInviterTotal()
        },
        update() {
            this.closeUpdate()
            timer = setTimeout(() => {
                this.getData()
                this.update()
            }, 5000)
        },
        closeUpdate() {
            clearTimeout(timer)
            timer = null
        }
    },
    destroyed() {
        this.closeUpdate()
    }
}
</script>

